const PostComponent: PostComponentWithMetadata = () => {
  const { isDay } = useContext(DayNightContext);

  const formattedDate = format(parseISO(DATE), 'dd-MM-yyyy');

  return (
    <div className={`max-w-4xl mx-auto rounded-md p-4 border-l-4 ${isDay ? 'bg-primary text-secondary border-secondary' : 'bg-secondary text-primary border-primary'}`}>
      <h1 className={`text-4xl font-bold mb-2 ${isDay ? 'text-secondary' : 'text-primary'}`}>{TITLE}</h1>
      <p className="text-sm text-gray-500">{formattedDate}</p>
      <p className="text-sm mb-10 mt-5 text-gray-500">Cancin sugerida al leer: <i>"The Earth Prelude"-Ludovico Einaudi</i></p>
      
      <p className="mb-4"> Cuando era nio, sola mirar las estrellas, impactado, asombrado e incluso completamente silenciado cuando la inmensidad del cosmos me minimizaba con su majestuosidad. Poco despus de haber contemplado por primera vez la inmensidad del cosmos desde el patio de mi casa, lo primero que pens fue: <strong>Qu existe all afuera?</strong> Sin embargo, no era una pregunta tan fcil de responder. Mi curiosidad me llev a la biblioteca de mi escuela primaria "20 de Noviembre", donde consegu un libro que, segn recuerdo, se llamaba "El sistema solar y el cosmos". Aunque la portada mostraba el sistema solar, si mi memoria no me falla, el contenido se enfocaba principalmente en el sistema solar. Recuerdo que mencionaban que Plutn era el planeta ms pequeo del sistema, lo cual me impact enormemente. Cada imagen que vea en el libro me haca reflexionar sobre lo poco que conoca de lo que significa vivir. Entonces, decid que cuando me preguntaran sobre mi futura profesin, respondera <strong>astronauta</strong>, con la intencin de satisfacer mi curiosidad de saber: <strong>Qu ms hay all afuera?</strong>. Recuerdo que la gente sola decir que era muy difcil, pero que lo podra lograr; en su mayora, quienes me alentaban eran familiares, por lo que senta al menos un poco de apoyo. Tanto as que un to sola invitarme a ver la serie <strong>"Cosmos"</strong> de Neil DeGrasse Tyson, cuya personalidad, incluso en su edad adulta al momento de haber hecho la serie, se comparaba con la ma: pareca que nada poda satisfacer esa sed de conocer el mundo.</p>

      <p className='mb-4'>Crec bajo la influencia de personas que, desde nio, fueron una fuente tremenda de inspiracin en el mbito del conocimiento. Recuerdo que, cuando le preguntaba algo a mi padre, l intentaba responderme de manera que me hiciera ver que su respuesta era solo una entre muchas posibles. Siempre me dejaba con ms preguntas que respuestas, cosa que, con el tiempo, entend que fue lo mejor que pudo haber hecho conmigo. Despus, un primo al que llamar George fue una persona que me llevaba algunos aos. Sin embargo, cada vez que lo vea, me deleitaba con un tema nuevo. Sola hablar por horas sobre temas variados, llenos de ciencia o incluso misticismo, pero su forma de hablar era lo que siempre admir, porque conocer era lo que lo motivaba. Sola investigar sobre algo cuando no lo saba, especialmente si era algo que yo conoca y l no. Y no lo culpo, todos los que tenemos un poco de esta chispa sentimos que no es cuestin de que otro sepa ms que nosotros, sino que impresiona tanto que se siente como si nos estuviramos perdiendo de algo. Otra persona importante para m fue un to muy cercano a mi pap y mi mam, pero que dej una huella que de nio me doli mucho hasta que la entend. Me dijo: "No puedes ensearle nada a un adulto". El contexto de esa pltica surgi porque, cuando era muy nio, sola tener buenas calificaciones, tanto que un 9 me pesaba lo mismo que un 6; para m era intolerable. Un da, en una fiesta familiar a la que no quera ir porque al da siguiente tena escuela, mientras mi madre intentaba despejarse del estrs de la rutina, me encontr en una reunin llena de alcohol y cigarrillos. Ante mis ojos, solo vea personas intentando ignorar la realidad, algo que iba muy en contra de mi recin nacida filosofa de buscar ms all de lo que mis ojos podan ver y por ese motivo me acerque a mi to a pedirle que si le deca algo a mi madre. Entonces, durante los siguientes 8 aos, decid seguir aprendiendo de todo y maravillndome con cada cosa, preguntando, explorando. En esa etapa viva a las afueras de la ciudad, a 40 minutos, sin vecinos, donde solo ramos mi hermano y yo contra el mundo. Jugbamos, cavbamos, llenbamos nuestras ropas de lodo al andar en bicicleta en poca de lluvias, coleccionbamos caracoles en botellas de Coca-Cola y hacamos de nuestro patio un mundo lleno de posibilidades. Despus de aquel comentario de mi to, mi lucha fue convertir su afirmacin en una promesa contra la ignorancia. Cabe aclarar que, al decir "ignorancia" en este contexto, me refiero a las cosas que ignoramos o no conocemos, muchas veces no por eleccin, sino porque se pierden de nuestro campo de visin.</p>


    <p className='mb-4'>La magia es increble, literalmente, porque cuando descubres lo que hay detrs del truco, deja de ser interesante. As lo cre durante mi poca de preparatoria, donde mi hiperrealismo contamin mis sueos de nio, entendiendo que ser astronauta era solo un camino que quera continuar ms por capricho que porque realmente tuviera alguna posibilidad. Entre mis amistades, cuando llegaba a escuchar lo que queran ser, nada sonaba con el esmero o la pasin con la que yo sola hablar. Todo sonaba tan montono e incluso desmoralizador, que nunca tuve la oportunidad de reconsiderar el volver a plantearme ser astronauta, hasta tiempo despus. Durante la preparatoria, ese hiperrealismo me llev a dejar de soar, dejar de imaginar, dejar de soar despierto, dejar de <strong>mirar las estrellas</strong>, algo que sola hacer mucho cuando era nio. Lo que en mi historia de vida llamo "<strong>Mi edad media</strong>", ya que sent un estancamiento total en mi vida. Tanto as que de alguna manera desarroll una mana por resolver los puzzles mentales de cada persona, hacindome a la idea de que poda conocerlos aprendiendo de su historia de vida. As, poco a poco, lograba llegar con cada una de esas personas y listar mis descubrimientos acerca de ellas, al punto en que incluso disfrutaba ver cmo su expresin se tornaba molesta al saber que haba acertado. Una mala copia de Dr. Gregory House podramos decir. Pero saba, muy en el fondo, que no quera descubrir secretos de las personas, solo que an no saba por dnde buscar. Quera algo ms. Aparte de eso, me volv una persona odiosa y perd amistades de manera justificada. Pero toda luz tiene una gran sombra, sombra que jams ocultar. Sera estpido pensar que lo que soy hoy es gracias a una serie de situaciones y momentos limpios, perfectos y precisos, cuando es totalmente lo contrario. Soy una serie de incongruencias y desperfectos que, con el tiempo, molde para darle vida a mi yo actual, aceptando esta poca de oscuridad en mi saber y entendiendo que decir <strong>Qu ms hay all afuera?</strong> era solo una pequea parte de lo que poda hacer, y que mirar afuera no era necesariamente la nica manera que detonaba la curiosidad.</p>

    <p className='mb-4'>Mi hermano suele decir de broma que Canad es para m lo que Vietnam fue para Estados Unidos, y todo esto viene por una serie de eventos. Pero lo que realmente encontr fue la segunda pregunta que detonaba una curiosidad sin igual en m, tanto que se me erizaba la piel al hacerme esta pregunta. Y puede sonar raro para muchos; no los culpo. Yo tambin sola pensar que el mundo que me rodeaba era todo lo que conoca, y con eso bastaba. Hubo dos ingredientes para este descubrimiento: estar <strong>completamente solo</strong> y <strong>caminar tanto como pudiera</strong>. Estos dos ingredientes hicieron algo muy curioso; lo primero fue que estar en un pas donde las costumbres, paisajes, arquitectura, flora, fauna, comida y, principalmente, el idioma eran diferentes, me haca sentir muy lejos de casa. Y si a ese sentimiento le sumas el hecho de que realmente ests solo, pues, aunque las personas a tu alrededor se cuenten por docenas o cientos, basta con pensar que nadie te mira. Peor an, en algunas aventuras que tuve en los bosques que rodeaban la ciudad, no solo se trataba de un sentimiento, sino que realmente haba momentos en los que pasaban 40 minutos sin que viera a una persona desde que me adentraba en los bosques llenos de arces y pinos junto con esa maleza hermosa que an recuerdo. Y aqu es donde se junta el segundo ingrediente. Mencion 40 minutos, pero ese no era el tiempo que caminaba en un da; caminaba un promedio de 29 kilmetros por da y, en los mejores das, hasta 32, que fue mi rcord. Eran horas de caminar, caminar y caminar. Es necesario mencionar que, cuando uno va en carretera en un viaje de 2 o 3 horas, el paisaje cambia tan rpido que no nos da tiempo de ver detalles. Y mucho menos si miramos de cerca, porque las lneas discontinuas se vuelven continuas dependiendo de la velocidad a la que vamos. Pero seguro que no queremos que un viaje de 180 kilmetros nos tome un da cuando podemos hacerlo en 3 horas, cierto? Bueno, caminar tales distancias no presenta ese problema; tanto que surge otro despus de desmenuzar el paisaje, desde las construcciones hasta las piedras que sobresalen del concreto en la calle o la textura de las hojas al caminar sobre ellas. Incluso llegaba a comparar las copas de los rboles para saber cul es ms alto y, posiblemente, ms viejo. Llega un momento en el que la mente deja de ver hacia afuera y es cuando dej de preguntarme <strong>Qu hay all afuera?</strong> y pas a preguntarme <strong>Qu hay aqu adentro?</strong>. Esa pregunta lo cambi todo. Dejar de ver hacia afuera para mirar dentro hizo que pensara que apenas si me conoca. Pero cmo?! Si yo he habitado este cuerpo por aos y, por obvias razones, jams me he separado de l. Esto me llev a ver que dentro de m haba un universo que, aunque no tuviera un traje o una nave espacial, poda visitar de vez en cuando y cada vez ms a menudo. As, la introspeccin se convirti en mi pasatiempo favorito, perdindome en mis rincones internos. Hay una parte de la cancin de Calle 13 que se llama <i>Me vieron cruzar</i> que dice: "Pero soy un barrilete csmico, lo ms grande", la cual fue mi himno ante este descubrimiento. Aunque para ese momento, el rock, que haba sido mi gnero maestro, dej de gustarme, ya que buscaba algo que acompaara mis pensamientos y me apoyara en mi descubrimiento sin manipulacin y sin desviarme de mi introspeccin, como las canciones acompaan a los grandes films. Fue entonces cuando conoc a Ludovico Einaudi y a Tony Anderson, a quienes estimo porque, gracias a sus complejas notas, pude entender la inmensidad de mi propio desconocimiento. Por segunda ocasin en mi vida, me invadi esa sensacin de asombro, majestuosidad e intriga, como cuando sola ver las estrellas de nio, y all dije nuevamente: <strong>Quiero ser astronauta</strong>. Solo que ahora no hablaba de la profesin, sino de lo que hacen: prepararse toda una vida con un entrenamiento riguroso y especializado para soportar lo que una persona normal no podra, como las presiones G, la movilidad limitada, o la gravedad que se comporta diferente, donde la idea de "lugar para vivir" cambia por completo, donde solo unos pocos podran llegar y ver con sus propios ojos lo que para otros son solo teoras y conspiraciones. S que no son dioses, pero cuando en tiempos antiguos apuntbamos al cielo, muchas veces no era para sealar las estrellas. Yo suelo hacerlo, pero no para saludar a los dioses, sino para recuperar mi rumbo.</p>

    <p>Esto que relato fue el comienzo de cmo pude volver a querer ser ese astronauta, la idea de lo imposible e incluso ms all de las estrellas, y cmo un sueo de nio fue lo que me salv la vida. Si no me hubiera expuesto a esto, quiz este blog no existira, y es posible que estara jodiendo la vida a las personas de manera inconsciente. Y por eso, por si un da olvido este sueo otra vez y vuelve a existir una poca oscura, o como yo la llamo <strong>Mi edad media</strong>, solo bastar con ver mis tatuajes. Estos representan esta parte de mi vida y mi descubrimiento: nunca dejar de conocer, aprender, maravillarse y, principalmente, siempre apuntar a las estrellas.</p>

    </div>
  );
};

